.clear-btn
{
    display:flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size:1vw;
    height:4vw;
     width:8vw;
     border-radius:38%;
     border: .5vw solid white ;
     color:white;
     background-color:rgb(34, 226, 233);
     margin:.5vw;  
}
.clear-btn:hover{
    transition:0.3s;
    color:white;
    border: .5vw solid white;
     background-color: #00ff00;

}
@media(max-width:775px)
{
    .clear-btn{
        font-size:5vw;
        height:20vw;
         width:28vw;
         margin:3vw;
  }
  
}